.page {
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    font-family: Roboto, sans-serif;
    background-color: #fff;
  }

  &__menu {
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: 0.5s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }

    &-title {
      margin-bottom: 32px;
      font-weight: bold;
      font-size: 36px;
      line-height: 36px;
      text-align: center;
      letter-spacing: -1.5px;
      color: #000;

      @include onDesktop {
        margin-bottom: 56px;
        font-size: 48px;
        line-height: 48px;
      }

      &::after {
        content: "";
        display: block;
        width: 23px;
        height: 16px;
        margin: 4px auto 0;
        background-image: url(../images/crown.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }
}
